import React, {Component} from 'react'
import './cart.css'




class Cart extends Component{
	constructor(props){
		super(props)
		this.state = {visible:"block",items:[{id:1,name:"Nike Shoes X",price:200,qty:1}]}
			
	}
	
	clickCart(){
		console.log('asd')
		if(this.state.visible == "block")
		{
			this.setState({visible:"none"})
		}else{
			this.setState({visible:"block"})
		}
	}
	
	
	renderProductsHTML(){
		let list = this.state.items
		let html = list.map((item,key) => 
			<div className="cartProduct"><img src={require('../img/items/'+item.id+'.jpg')}></img><div className="cartItemName">{item.name}</div><div className="closeBtn2"><img src={require('../img/x.png')}></img></div><div className="cartItemPrice">{item.price} EUR</div><div className="itemqty">Quanity:{item.qty}</div></div>
		
		)
				
		
		return(html)
	}
	
	delItem(id){
		
	}
	
	render(){
		

		
		return(
			<div className="Cart" style={{display:this.state.visible}}>
			
				<div className="Title">Your shopping cart<span className="closeBtn"><img src={require('../img/x.png')} onClick={() => this.clickCart()}></img></span></div>
				<div className="blank"></div>
				{this.renderProductsHTML()}

			<div className="total">Total price: 54545 EUR</div>
			<div className="checkout">
			
			Pay Now
			</div>
			
			</div>
		
		)
	}
}


export default Cart;


