import React,{Component} from 'react';
import './product.css';



class Product extends Component{
	constructor(){
		super()
	}
	
	
	render(){
		return(
			<div className="Item">
				<div className="item_data">
					<div className="itemPhoto">
						<img src={require('./img/items/1.jpg')}></img>
					
					</div>
				<div className="itemInfo">
					
					<div className="item_title">Nike shoes</div>
					<div className="item_cat">Shoes</div>
					<div className="item_price">200 EUR</div>
					<div className="item_avaiable">Avaiable in stock</div>
					
					<div className="item_qty">Quantity: <input value="1" style={{width:30}}></input></div>
					
					
					<button>Dodaj do koszyka</button>
					
				</div>
					<div className="otherpics">
						<img src={require('./img/items/1.jpg')}></img>
						<img src={require('./img/items/1.jpg')}></img>
						<img src={require('./img/items/1.jpg')}></img>
					</div>

					
				</div>
				
				

				
				
			
			</div>
		)
	}
	
}

export default Product